:root {
  --main-grean-color: #28a745;
  --main-white-color: #fff;
  --blue-color: #0284d0;
  --bg-color-white: #fff;
  --bg-form-callback: #f4f4f4;
  --text-color-dark: #333;
  --text-color-white: #fff;
  --text-hero: #e9e9e9;
  --text-placeholder: gray;
  --gradient: rgba(0, 0, 0, .6);
  --box-shadow: 2px 2px 12px 2px rgba(0, 0, 0, .4);
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

.card-set {
  row-gap: var(--gap-top);
  column-gap: var(--gap-left);
  flex-wrap: wrap;
  display: flex;
}

.card-item {
  flex-basis: calc((100% - var(--card-colum) * var(--gap-left) ) / var(--card-item) );
}

html {
  scroll-behavior: smooth;
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

